/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #232f3e;
  color: white;
  font-family: Arial, sans-serif;
}

.App {
  width: 100%;
  min-height: 100vh;
  text-align: center;
}

/* Header styles */
.header {
  padding: 2rem 0;
}

/* Content container for centering */
.content {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0 1rem;
}

/* Question box styles */
.question-box {
  background-color: #f2f3f3;
  border-radius: 4px;
  margin: 0.25rem auto;
  width: 100%;
  max-width: 600px;
}

.question-box-inner {
  background-color: white;
  padding: 0.75rem 1rem;
  border-radius: 4px;
  color: #16191f;
}

/* Select dropdown styles */
select {
  width: 100%;
  max-width: 600px;
  padding: 0.5rem;
  margin: 1rem auto;
  border-radius: 4px;
  text-align: center;
  text-align-last: center;
  -moz-text-align-last: center;
  -webkit-text-align-last: center;
}

/* Button styles */
button {
  background-color: white;
  color: #232f3e;
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 4px;
  margin: 0 0.5rem;
  cursor: pointer;
}

button:hover {
  background-color: #f0f0f0;
}

/* Text styles */
h1 {
  font-size: 3rem;
  font-weight: 300;
  margin-bottom: 1rem;
}

h2 {
  font-size: 2rem;
  font-weight: 300;
  margin-bottom: 1rem;
}

p {
  margin-bottom: 1rem;
}

/* Button container spacing */
.button-container {
  margin: 1.5rem 0 3rem 0;
}

/* Category and key points spacing */
.category-section {
  margin-top: 3.5rem;
  margin-bottom: 2rem;
}

.key-points-section {
  margin-top: 3.5rem;
}

/* Ensure consistent spacing between sections */
.section-spacing {
  margin-bottom: 2.5rem;
}

/* Add specific spacing for the category title */
.category-title {
  margin-top: 4rem;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
}

/* Responsive text sizes */
@media (max-width: 768px) {
  h1 {
    font-size: 2rem; /* Smaller title on mobile */
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 1rem;
  }

  .question-box-inner {
    padding: 0.5rem;
  }

  /* Adjust button container for mobile */
  .button-container {
    flex-direction: column;
    gap: 1rem;
  }

  button {
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
  }

  /* Adjust content padding */
  .content {
    padding: 0 1rem;
  }
}

/* Even smaller screens */
@media (max-width: 480px) {
  h1 {
    font-size: 1.75rem;
  }

  .amazon-logo {
    height: 24px; /* Smaller logo on mobile */
  }
}
